.outer {
  position: relative;
  overflow: hidden;
  height: 100%;
}

.container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 16px;
  height: 100%;
  overflow-y: auto;
  padding-right: 300px;
}

.main {
  overflow-y: auto;
  flex: 1;
  padding: 32px 48px;
}

.main pre {
  margin: 0;
}

.tableOfContents {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  box-shadow: inset 1px 0 0 0 var(--color-keyline-default);
  margin-top: 24px;
  padding: 0 16px 0 16px;
}

.tableOfContents ol {
  padding: 8px 16px;
  margin-left: 0;
}

.tableOfContents li,
.tableOfContents li a {
  color: var(--color-text-default);
}

.tableOfContents li:has(a:hover) {
  color: var(--color-text-light);
}

.tableOfContents a:hover {
  color: var(--color-text-light);
  text-decoration: none;
}

.tableOfContents li:hover a {
  text-decoration: none;
}
